.app {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px 24px;
}
.app-searchform {
  margin-top: 18px;
}
.app-input {
  padding: 6px 16px;
  min-width: 200px;
  max-width: 100vh;
  font-size: 15px;
}
.app-submit {
  padding: 6px 16px;
  font-size: 15px;
  margin-left: 6px;
  background-color: rgb(55, 146, 198);
  border: 2px solid rgb(55, 146, 198);
  border-radius: 4px;
  color: white;
  cursor: pointer;
}
.app-submit:hover {
  padding: 6px 16px;
  font-size: 15px;
  margin-left: 6px;
  background-color: white;
  border: 2px solid rgb(55, 146, 198);
  border-radius: 4px;
  color: rgb(55, 146, 198);
  cursor: pointer;
}
.app-recipes {
  margin-top: 24px;
  display: grid;
  grid-template-columns: 230px 230px;
}
